<template>
  <el-card>
    <template #header>
      <div class="flex flex-row justify-between">
        <span>编辑影厅</span>
        <router-link class=" text-blue-500" :to="{name:'HallInfoList'}">返回列表</router-link>
      </div>
    </template>
    <el-skeleton :rows="5" animated :loading="isLoading">
      <el-form :model="formData" :rules="formDataRules" label-width="120px" ref="formEl">
        <el-form-item label="影厅名称" prop="hall_name">
          <el-input placeholder="请输入影厅名称" v-model="formData.hall_name" />
        </el-form-item>
        <el-form-item label="影厅标签" prop="tags">
          <el-select multiple
            filterable
            allow-create
            default-first-option
            :reserve-keyword="false"
            placeholder="请选择影厅标签"
            v-model="formData.tags"
            class="w-full">
            <el-option :label="item" :value="item" v-for="item in ['退','改签','折扣卡','4K厅','全景声','IMAX厅','小吃','杜比全景声']" :key="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="影厅地址" prop="address">
          <el-input placeholder="影厅地址" v-model="formData.address" />
        </el-form-item>
        <el-form-item label="影厅坐位" prop="sites">
          <div class="w-full mt-[-5px]">
            <el-button-group class="my-[10px]">
              <el-button type="info" @click="addNewRow" :icon="CirclePlus">添加一行</el-button>
              <el-button type="success" @click="copyLastRow" :icon="DocumentCopy">复制一行</el-button>
            </el-button-group>
            <el-row class="mt-[10px]">
              <el-col :span="12" class=" flex flex-row text-[14px]  items-center">
                <div class="w-[30px] h-[26px]" draggable="true" data-site="0" @dragstart="siteDrogStart($event,0)">
                  <img src="../../assets/img/00.png" alt="">
                </div>
                <span class="ml-[5px]">可选</span>
              </el-col>
              <el-col :span="12" class=" flex flex-row text-[14px]  items-center">
                <div class="w-[30px] h-[26px] border border-dashed border-gray-500" data-site="-1" draggable="true" @dragstart="siteDrogStart($event,-1)">
                </div>
                <span class="ml-[5px]">空位</span>
              </el-col>
            </el-row>
            <ul class="site-list">
              <li
                @dragend.prevent="" @dragover.prevent=""
                @drop.prevent="siteItemDrop($event,index)"
                v-for="(item,index) in siteDetail" :key="index">
                <el-icon color="red" :size="22" class="absolute right-[10px] cursor-pointer" @click="deleteCurrentRow(index)">
                  <CircleCloseFilled />
                </el-icon>
                <div
                  class="mr-[5px] w-[30px] h-[26px] border  border-gray-500"
                  :class="{'border-dashed':site==-1}"
                  v-for="(site,index2) in item" :key="index2">
                  <img src="../../assets/img/00.png" v-if="site==0" />
                </div>
              </li>
            </ul>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" :loading="isSubmiting">保存数据</el-button>
          <el-button type="danger" @click="$router.back()">返回列表</el-button>
        </el-form-item>
      </el-form>
    </el-skeleton>
  </el-card>
</template>
<script setup>
import { ref, reactive, watch, onMounted } from "vue";
import {
  CircleCloseFilled,
  CirclePlus,
  DocumentCopy,
} from "@element-plus/icons-vue";
import { ElMessageBox, ElMessage } from "element-plus";
import API from "@/utils/API";
import { useRoute, useRouter } from "vue-router";

const route = useRoute();
const router = useRouter();
const formData = ref({
  id: "",
  hall_name: "",
  tags: [],
  sites: "",
  address: "",
});

const siteDetail = ref([]);

watch(
  () => siteDetail,
  (newValue, oldValue) => {
    formData.value.sites = JSON.stringify(siteDetail.value);
  },
  { deep: true }
);

const formDataRules = {
  hall_name: [{ required: true, message: "请输入影厅名称", trigger: "blur" }],
  tags: [{ required: true, message: "请输入影厅标签", trigger: "blur" }],
  sites: [{ required: true, message: "请设计影厅座位", trigger: "blur" }],
  address: [{ required: true, message: "请输入影厅地址", trigger: "blur" }],
};

const formEl = ref(null);

//根据id查询，将数据填充到表单
const isLoading = ref(false);
const findById = () => {
  isLoading.value = true;
  formData.value.id = route.params.id;
  API.hallInfo
    .findById(formData.value.id)
    .then((result) => {
      formData.value = result.data;
      formData.value.tags = result.data.tags.split(",");
      siteDetail.value = JSON.parse(result.data.sites);
    })
    .finally(() => {
      isLoading.value = false;
    });
};
findById();

// 添加一行
const addNewRow = () => {
  siteDetail.value.push([]);
};

// 删除一行
const deleteCurrentRow = (index) => {
  ElMessageBox.confirm("确定要删除这一行吗？", "请确认", {
    confirmButtonText: "确定",
    cancelButtonText: "取消",
    type: "warning",
  }).then(() => {
    siteDetail.value.splice(index, 1);
  });
};

//复制一行
const copyLastRow = () => {
  if (siteDetail.value.length > 0) {
    const lastRow = siteDetail.value[siteDetail.value.length - 1];
    siteDetail.value.push([...lastRow]);
  } else {
    ElMessage.error("请先添加一行");
  }
};

const siteDrogStart = (event, site) => {
  event.dataTransfer.setData("site", site);
};

//手动以后放入的事件
const siteItemDrop = (event, index) => {
  var site = event.dataTransfer.getData("site");
  siteDetail.value[index].push(+site);
};

//提交表单
const submitForm = () => {
  formEl.value.validate((valid) => {
    if (valid) {
      saveDate();
    } else {
      ElMessage.error("请将数据填写完整");
    }
  });
};

//提交表单以后保存数据的操作
const isSubmiting = ref(false);
const saveDate = () => {
  isSubmiting.value = true;
  API.hallInfo
    .update(formData.value.id, {
      ...formData.value,
      tags: formData.value.tags.join(","),
    })
    .then((result) => {
      ElMessage.success("数据保存成功");
      router.replace({ name: "HallInfoList" });
    })
    .finally(() => {
      isSubmiting.value = false;
    });
};

//重置表单
const resetForm = () => {
  formEl.value.resetFields();
  formData.sites = "";
  siteDetail.value = [];
};
</script>
<style scoped lang="scss">
.site-list {
  counter-reset: row 0;
  > li {
    counter-increment: row 1;
    @apply h-[40px] border border-solid border-gray-300 flex items-center mt-[10px] relative;
    &::before {
      content: counter(row) "排";
      @apply text-[12px] w-[60px] flex flex-row justify-center items-center font-bold;
    }
  }
}
</style>